.main {
  width: 100%;
  min-height: 96vh;
  background: url('../Images/mainBackground.jpg') no-repeat center/cover;
  display: flex;
  position: relative;

  .container {
    margin: auto;
    text-align: center;
    z-index: 2;
    color: #fff;
  }

  @media (max-width: 965px) {
    .container {
      max-width: 90%;
    }
  }
}