.trainers {
  width: 100%;
  padding: 160px 0;
  text-align: center;
  position: relative;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;

  .title {
    position: relative;
    z-index: 5;
    @media (max-width: 650px) {
      padding: 0 40px;
    }
  }

  .container {
    display: flex;
    justify-content: space-between;
    z-index: 6;
    position: relative;
    padding: 80px 0 0 0;

    @media (max-width: 769px) {
      max-width: 90%;
      flex-direction: column;
      align-items: center;
    }
    @media (min-width: 770px) and (max-width: 1200px) {
      max-width: 90%;
      flex-wrap: wrap;
    }
  }
}