.trainerCard {
  color: #fff;

  .trainerImg {
    position: relative;
    img {
      width: 100%;
      max-width: 450px;
      height: auto;
      display: block;
      -webkit-filter: grayscale(100%);
      filter: grayscale(100%);
    }
  }

  &:hover {
    .cardOverlay {
      opacity: 1;
      height: 100%;
      transition: opacity 400ms ease-in-out, height 400ms ease-in-out;
    }
  }

  h2 {
    font-size: 26px;
    font-weight: 900;
    line-height: 1;
    margin: 14px 0 0 0;
  }

  p {
    font-size: 16px;
    margin: 16px 0 0 0;
  }

  @media (max-width: 769px) {
    width: 100%;
    max-width: 500px;
    margin-bottom: 30px;
    .trainerImg {
      img {
        width: 100%;
      }
    }
  }

  @media (min-width: 770px) and (max-width: 1200px) {
    width: 100%;
    max-width: 47%;
    margin-bottom: 30px;
    .trainerImg {
      img {
        width: 100%;
      }
    }
  }
}